import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserDataService {

  readonly subject: BehaviorSubject<any>

  constructor() {
    this.subject = new BehaviorSubject(null)
  }

  data: {} = {}

  userData(email: string):void {
    this.data['email'] = email
    this.next()
  }

  userFavoritSize(size: number) {
    this.data['favorites'] = size
    this.next()
  }

  userCartSize(size: number):void {
    this.data['cart'] = size
    this.next()
  }

  emitTab(tab: string):void {
    this.data['tab'] = tab
    this.next()
  }

  detailsData(data: {}):void {
    this.data['details'] = data
    this.next()
  }

  isAdmin(admin: boolean):void {
    this.data['admin'] = admin
    this.next()
  }
  signOut():void {
    this.subject.next(null)
  }

  private next():void {
    this.subject.next(this.data)
  }

}
