import { Component } from '@angular/core';
import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore } from '@angular/fire/firestore';

import { UserDataService } from './services/user-data.service';


@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private afAuth: AngularFireAuth,
    private firestore: AngularFirestore,
    private userData: UserDataService,
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  ngOnInit() {
    this.auf()
  }

  auf() {

    this.afAuth.auth.onAuthStateChanged((user) => {
      if (user) {
        // console.log(user)
        user.getIdTokenResult().then(e => localStorage.setItem('token', e.token))

        this.userData.userData(user.email)
        this.getCartSize(user)
        this.getFavoritSize(user)
        this.firestore.collection("admins").doc('eQbHkjgcwiGpCqwWHyzj').get().subscribe(
          data => {
            if (data.data()[user.email]) {
              this.userData.isAdmin(true)
              console.log('admin')
              return
            }
            return
          }
        )
      } else {
        console.log('no auth')
        localStorage.setItem('token', null)
        this.userData.isAdmin(false)
        let favoritObj = JSON.parse(localStorage.getItem('favorites')) || {}
        this.userData.userFavoritSize(Object.keys(favoritObj).length)
        let cartObj = JSON.parse(localStorage.getItem('cart')) || {}
        this.userData.userCartSize(Object.keys(cartObj).length)
        this.userData.userData('guest')
      }
    });
  }


  getCartSize(e) {
    this.firestore.collection('usersData')
      .doc(e.email)
      .collection('cart')
      .valueChanges()
      .subscribe(
        e => {
          let size = e.length ? e.length : null
          this.userData.userCartSize(size)
        })
  }
  getFavoritSize(e) {
    this.firestore.collection('usersData')
      .doc(e.email)
      .collection('favorites')
      .valueChanges()
      .subscribe(
        e => {
          let size = e.length ? e.length : undefined
          this.userData.userFavoritSize(size)
        })
  }



}
