import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-admin-card',
  templateUrl: './admin-card.component.html',
  styleUrls: ['./admin-card.component.scss'],
})
export class AdminCardComponent implements OnInit {

  constructor() { }

  ngOnInit() {}
  @Input() items
  @Input() token

  setItem(item) {
    let items = JSON.stringify(item)
    let token = JSON.stringify(this.token)
    localStorage.setItem('item', items)
    localStorage.setItem('token', token)
  }


  url(img, token) {
    return `https://firebasestorage.googleapis.com/v0/b/shopping-backend-firebas-8d9fe.appspot.com/o/images%2F${img}?alt=media&token=${token}`
  }
}
